@import './variables';

html {
    body {
        font-family: 'Poppins', sans-serif;
        font-size: 14px;
        background: $bgColor;
    }

}

#root {
    * {
        ::-webkit-scrollbar-track {
            background: #ddd;
            border-radius: 0;
        }

        ::-webkit-scrollbar-thumb {
            border-bottom: 4px solid $gray;
            width: 160px;
        }

        ::-webkit-scrollbar {
            height: 4px;
        }
    }
}

$colors: ("0": #3772FF, "1":#FD9821, "2": #BF37FF, "3":#FF37A3, "4":#37FFB7, "5":#F43F3F, "6":#37B7FF, "7":#3772FF, "8":#FD9821, "9":#BF37FF, "10":#FF37A3, "11":#37FFB7, "12":#F43F3F, "13":#37B7FF, "14": #3772FF, "15":#FD9821);
@each $ndx, $color in $colors {
    .tr-indent-#{$ndx} {
        &::after {
            content: "";
            width: 1px;
            height: 56px;
            display: inline-block;
            background: $color;
            background: transparent;
        }

    }
  }




button.view-raw {
    color: $blue;
    font-size: 14px;
    text-transform: none;
}

.curcular-wrapper {
    display: flex;
    justify-content: center;
}

.VictoryContainer {
    svg>g:nth-child(3) {
        transform: translate(-23px, 0px);
    }
}


@each $ndx, $color in $colors {
    .td-label-#{$ndx} {
        &::after {
            content: "";
            width: 7px;
            height: 41px;
            display: inline-block;
            background: $color;
            position: absolute;
            left: 0px;
            top: 13%;
        }
    }
}

@media screen and (max-width: 375px) {
    #x-axis, #y-axis  {
        font-size: 12px;
    }
}

@media screen and (min-width: 768px) {
    @each $ndx, $color in $colors {
        .td-label-#{$ndx} {
            &::after {
                content: "";
                width: 7px;
                height: 41px;
                display: inline-block;
                background: $color;
                position: absolute;
                left: 8px;
                top: 20%;
            }
        }
    }
}

@media screen and (min-width: 1280px) {
    @each $ndx, $color in $colors {
        .td-label-#{$ndx} {
            &::after {
                content: "";
                width: 7px;
                height: 41px;
                display: inline-block;
                background: $color;
                position: absolute;
                left: 8px;
                top: 15%;
            }
        }
    }
}

