.table-scroll__wrapper {
    overflow-x: auto;
    transform: translateY(60px);
    //margin: 0 32px;
    // position: absolute;
    // top: 1090px;

    .scroll-content {
        width: 1217px;
        height: 20px;
    }

    ::-webkit-scrollbar-track {
    }

    ::-webkit-scrollbar-thumb {
    }

    ::-webkit-scrollbar {
    }

}

.left-button {
    all: unset;
    position: relative;
    width: 36px;
    height: 36px;
    &::before {
        content: url("../assets/arrowLeft.svg");
        display: inline-block;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 20%;
        left: 35%;
    }
}

.left-button.active {
    border: 2px solid #E6E8EC;
    border-radius: 40px;
}


.right-button {
    all: unset;
    position: relative;
    width: 36px;
    height: 36px;

    &::after {
        content: url("../assets/arrowRight.svg");
        display: inline-block;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 20%;
        left: 35%;
    }
}

.right-button.active {
    border: 2px solid #E6E8EC;
    border-radius: 40px;
}

.left-button, .right-button {
    cursor: pointer;
}

@media screen and (min-width: 768px) {
    .table-scroll__wrapper {
        overflow-x: auto;
        transform: translateY(60px);
        .scroll-content {
            width: 1217px;
            height: 20px;
            display: none;
        }
    }
}

.editor-scrollable {
    margin: 32px;
}

